 }
}


.container {
	margin: 0 auto;
	display: block;
	width: 1160px;
}
.page-content {
	@extend .d-block;
}
.wrapper {
	@extend .d-block;
	overflow: hidden;
}


.main-bn {
	@extend .d-block; 
	min-height: 800px;
	position: relative;
	background: url($img_dir+"lclc.jpg") no-repeat top center;
	background-size: cover;
	overflow: hidden;

	&::before {
		@extend .before;
		width: 100%;
		left: 0;
		top: 0;
		background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
		opacity: 0.7;
		height: 200px;		
		z-index: 1;
	}
	.header-container {
		position: relative;
		z-index: 5;
	}

	&::after {
		@extend .before;
		background: url($img_dir+"sprites/svg/circle-blur.svg") no-repeat center;
		opacity: .65;
		width: 670px;
		height: 575px;
		top: 50px;
		right: 24%;
	 z-index: 1;
		background-size: cover;
	}			

}
.main-bn-wrap {
	@extend .d-block;
	position: relative;
	
	@media (min-width: 960px) {
		&::before {
			content: "";
			display: block;
			position: absolute;
			z-index: 3;
			right: 0;
			top: -35px;
			background: url($img_dir+"sprites/svg/union.svg") no-repeat;
			width: 600px;
			height: 829px;
			background-size: cover;
		}	
	}

}
.main-bn-text {
	display: none;
	
	@media (min-width: 768px) {
		display: block;
		position: absolute;
		z-index: 5;
		bottom: 19px;
		left: 50%;
		width: 4550px;
		margin-left: -2275px;
		background: url($img_dir+"sprites/svg/text.svg") no-repeat center;
		height: 16px;
		backface-visibility: hidden;
		animation: sbs-anim-rec 15s infinite linear;	
	}
}
@-webkit-keyframes sbs-anim-rec {
	0% {
		opacity:1;
		transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);
		animation-timing-function:linear;
	}
	100% {
		opacity:1;
		transform:translateX(-1523px)translateY(0px)rotate(0deg)scale(1,1);
	}
}
@keyframes sbs-anim-rec {
	0% {
		opacity:1;
		transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);
		animation-timing-function:linear;
	}
	100% {
		opacity:1;
		transform:translateX(-1523px)translateY(0px)rotate(0deg)scale(1,1);
	}
}

