.text-anime-container {
	@extend .d-block;
	background: $color-blue2;
	overflow: hidden;
	padding: 4px 0 3px; 
}
@-webkit-keyframes text-anime-one {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(486px)translateY(0px)rotate(0deg)scale(1,1);}
}
@keyframes text-anime-one {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(486px)translateY(0px)rotate(0deg)scale(1,1);}
}

@-webkit-keyframes text-anime-two {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(-332px)translateY(0px)rotate(0deg)scale(1,1);}
}
@keyframes text-anime-two {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(-332px)translateY(0px)rotate(0deg)scale(1,1);}
}

@-webkit-keyframes text-anime-three {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(431px)translateY(0px)rotate(0deg)scale(1,1);}
}
@keyframes text-anime-three {
	0% {opacity:1;transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);animation-timing-function:linear;}
	100% {opacity:1;transform:translateX(431px)translateY(0px)rotate(0deg)scale(1,1);}
}


