}
.slider-style-white {
	.slick-arrow {
		width: 60px;
		height: 60px;
		border-radius: 30px;
		background: $color-white;
		transition: background 0.3s;
		z-index: 10;
		opacity: 1 !important;

		&:hover {
			background: $color-blue;

			&::before {
				opacity: 0 !important;
			}
			&::after {
				opacity: 1 !important;
			}
		}

		&::before, &::after {
			content: "" !important;
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			opacity: 1 !important;
			left: 0;
			top: 0;
			transition: opacity 0.3s;
		}
		&::after {
			opacity: 0 !important;
		}

		&.slick-prev {
			left: -80px;

			&::before {
				background: url($img_dir+"sprites/svg/icon-arrow-left.svg") no-repeat center;
			}
			&::after {
				background: url($img_dir+"sprites/svg/icon-arrow-left-white.svg") no-repeat center;
			}
		}
		&.slick-next {
			right: -80px;

			&::before {
				background: url($img_dir+"sprites/svg/icon-arrow-right.svg") no-repeat center;
			}
			&::after {
				background: url($img_dir+"sprites/svg/icon-arrow-right-white.svg") no-repeat center;
			}			
		}

	}

	.slick-dots {
		bottom: -50px;

		li {
			margin: 0;
			width: 30px;
			height: 30px;
			padding: 10px;

			button {
				width: 10px;
				height: 10px;
				background: $color-blue2;
				transition: background 0.3s;
				border-radius: 5px;

				&::before {
					display: none !important;
				}
			}
			
			&.slick-active, &:hover {
				button {
					background: $color-blue;
				}
			}
		}

	}
	&.slick-dotted.slick-slider {
		margin-bottom: 50px;
	}
} //- .slider-style-white

