		position: absolute;
		padding: 0 0 0 51px;

		&.triger-1 {
			top: 190px;
			left: -19px;
		}
		&.triger-2 {
			top: 447px;
			left: -19px;
		}
		&.triger-3 {
			top: 681px;
			left: -19px;
		}

		&.triger-4 {
			top: 123px;
			right: -34px;
		}
		&.triger-5 {
			top: 388px;
			right: -34px;
		}
		&.triger-6 {
			top: 635px;
			right: -34px;
		}
		&.triger-7 {
			top: 738px;
			right: 233px;
		}

		&.is-active {
			.year-triger-content {
				background: rgba($color-white, .5);
			}			
			.year-triger-text {
				opacity: 1;
				visibility: visible;
				overflow: visible;
				height: auto;
			}
		}
