.triger-color-blue {
	.year-triger-btn {
		background: rgba($color-blue2, 0.3);
		
		&::before {
			background: $color-blue2;
		}
	}
}
.year-triger-btn {
	width: 36px;
	height: 36px;
	display: block;
	border-radius: 50%;
	background: rgba($color-blue, .3);
	position: relative;
	float: left;
	margin-left: -51px;

	&::before {
		@extend .before;
		left: 50%;
		top: 50%;
		display: block;
		width: 24px;
		height: 24px;
		background: $color-blue;
		margin: -12px 0 0 -12px;
		border-radius: 50%;
	}
}






.year-triger-content {
	width: 245px;
	float: left;
	display: block;
	border-radius: 20px;
	z-index: 5;
}

.year-triger-title {
	@extend .d-block; 
	background: rgba($color-white, .7);
	border-radius: 25px;
	padding: 15px 18px 14px;
	color:  $color-blue5;
	font: 16px/21px $font-family-halvar-bold;
}
.year-triger-text {
	@extend .d-block;
	padding: 0;
	opacity: 0;
	height: 0;
	visibility: hidden;
	overflow: hidden;
	transition: opacity 0.3s, visibility 0.3s, overflow 0.3s;

	p {
		color:  $color-black;
		margin: 0;
		font: 12px/17px $font-family-halvar-regular;
	}
}
.main-video {
	@extend .d-block;
	margin-top: 200px;
}
.main-video-box {
	@extend .d-block;
	position: relative;
	border-radius: 30px;
	overflow: hidden;

	img {
		border-radius: 30px;
		float: left;
	}

	&::before {
		@extend .before;
		width: 100%;
		height: 100%;
		border-radius: 30px;
		left: 0;
		top: 0;
		z-index: 2;
		transition: background 0.3s;
	}

	&.is-play {
		&::before {
			background: $color-black;
		}
		.video-link {
			display: none;
		}
	}
}

.main-video-title {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	padding: 36px 46px 0;
	font: 54px/63px $font-family-halvar-bold;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: $color-white;
}
.video-link {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 5;
	background: url($img_dir+"sprites/svg/icon-play.svg") no-repeat center;
	transition: all 0.3s;

	&:hover {
		background-size: 110px 111px;
	}
}
.videoBox {
	background: #000;
	border-radius: 30px;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%; 
	z-index: 10;
}
.videoBox iframe,
.videoBox object,
.videoBox embed {
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%; 
