.btn-tl-show {
	cursor: pointer;
	right: 0;
	top: 50%;
	margin-top: -28px;
	position: absolute;
	z-index: 5;
	display: none;
	width: 56px;
	height: 56px;
	transition: transform 0.3s;	

	&::before, &::after {
		@extend .before;
		background: $color-blue;
		left: 50%;
		top: 50%;
	}
	&::before {
		width: 24px;
		height: 2px;
		margin-top: -1px;
		margin-left: -12px;
	}
	&::after {
		width: 2px;
		height: 24px;
		margin-top: -12px;
		margin-left: -1px;		
	}

	&.is-active {
		transform: rotate(-45deg);   	
	}

