	align-items: center;
}
.agenda__program-event-group-count-box {
	display: block;
	position: relative;
	padding-left: 19px;
	color: $color-grey3;
	font: 14px/16px $font-family-halvar-regular;
	
	&::before {
		@extend .before;
		left: 0;
		top: 0;
		background: url($img_dir+"sprites/svg/icon-person.svg") no-repeat;
		width: 11px;
		height: 14px;
	}
}
.agenda__program-event-btn {
	text-decoration: none;
	height: 40px;
	display: block;
	background: $color-blue2;
	border-radius: 7px;
	font: 14px/40px $font-family-halvar-bold;
	padding: 0 21px;
	color: $color-white;
	transition: background 0.3s, color 0.3s;

	&:hover, &.is-active {
		color: $color-blue2;
		background: rgba(23, 17, 139, 0.1);
	}

	&.is-active {
		&:hover {
			background: $color-blue2;
			color: $color-white;
		}
	}
}
.agenda__program-event-btn-trash {
	width: 40px;
	height: 40px;
	border-radius: 7px;
	margin-left: 10px;
	display: block;
	position: relative;
	text-decoration: none;
	background: $color-white;
	transition: background 0.3s;

	&::before, &::after {
		@extend .before;
		transition: opacity 0.3s;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}
	
	&::before {
		background: url($img_dir+"sprites/svg/icon-trash.svg") no-repeat center;
	}

	&::after {
		background: url($img_dir+"sprites/svg/icon-trash-white.svg") no-repeat center;
		opacity: 0;
	}

	&:hover {
		background: $color-blue2;

		&::before {
			opacity: 0;
		}
		&::after {
			opacity: 1;
		}
	}
}


