
footer {
	@extend .d-block; 
}
.footer-sponsor {
	@extend .d-block; 
	background: $color-white;
	padding: 88px 0 125px;
}
.footer-sponsor-left, .footer-sponsor-right {
	@extend .d-block; 
	width: 50%;
}
.footer-sponsor-title {
	@extend .d-block; 
	text-transform: uppercase;
	font: 36px/42px $font-family-halvar-bold;
	color: $color-blue2;
	letter-spacing: 1px;
	margin-bottom: 36px;
}

.footer-sponsor-right {
	padding-left: 70px;
	.footer-sponsor-title {
		color: $color-blue;
	}
}
.footer-sponsor-list {
	display: flex;
	margin: 0 -20px;

	li {
		width: 300px;
		padding: 0 20px;

		a {
			display: flex;
			height: 145px;
			width: 100%;
			justify-content: center;
			align-items: center;
			background: $color-blue-lite;
			text-decoration: none !important;

		}
		img {
			height: 68px;
		}
	}
}
.footer-help-list {
	li {
		img {
			height: 110px;
		}
	}
}
.icon-logo-footer {
	width: 202px;
	height: 76px;
}
.footer-blue-container {
	@extend .d-block; 
	background: $color-blue2;
	padding: 46px 0 49px;
}
.icon-fb {
	width: 24px;
	height: 24px;
}
.icon-instagram {
	width: 22px;
	height: 22px;
}
.icon-vk {
	width: 25px;
	height: 14px;
}
.icon-youtube {
	width: 24px;
	height: 17px;
}
.icon-telegram {
	width: 24px;
	height: 20px;
}
.footer-blue-content {
	display: flex;
	width: 100%;
}
.footer-blue-logo {
	flex-grow: 1;
	padding-top: 4px;
}
.footer-blue-history {
	width: 240px;
}
.footer-blue-social {
	width: 355px;
}
.footer-blue-title {
	font: 14px/16px $font-family-halvar-bold;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: $color-white;
	display: block;
}
.footer-history-list {
	display: flex;
	width: 100%;
	margin-top: 18px;

	li {
		margin-right: 28px;

		&:last-child {
			margin-right: 0;
		}

		a {
			display: inline-block;
			font: 14px/16px $font-family-halvar-bold;
			text-decoration: none;
			color: $color-white;
			border-bottom: 1px solid $color-blue;
		}

	}
}

.footer-social-list {
	display: flex;
	width: 100%;
	align-items: center;
	margin-top: 16px;

	li {
			display: flex;
			align-items: center;
			margin: 0 10px;

			&:first-child {
				margin-left: 0;
			}
			&:last-child {
				margin-right: 0;
			}
	}
}

.footer-contacts-container {
	@extend .d-block; 
	background: $color-violet;
	padding: 46px 0 24px;
}
.footer-contacts-list {
	display: flex;
	width: 100%;

	li {
		display: flex;
		flex-direction: column;

		a {
			display: inline-block;
			font: 16px/18px $font-family-halvar-bold;
			color: $color-white;			
			border-bottom: 1px solid $color-blue;
			padding-bottom: 6px;
		}

		&:nth-child(1) {
			width: 322px;
		}
		&:nth-child(2) {
			width: 260px;
		}
		&:nth-child(3) {
			width: 246px;
		}
		&:nth-child(4) {
			width: 214px;
		}
	}
}
.f-contact-title {
	font: 16px/18px $font-family-halvar-bold;
	color: $color-white;
	flex: 1;
}
.f-contact-email {
	margin-top: 10px;
}
.footer-bottom {
	width: 100%;
	float: left;
	display: flex;
	margin-top: 33px;
}
.copy {
	font: 16px/18px $font-family-inter-regular;
	color: $color-white;
	margin-right: 5px;
}
.footer-bottom-link {
	display: inline-block;
	font: 16px/18px $font-family-inter-regular;
	color: $color-white;
	border-bottom: 1px solid $color-blue;	
}

.is-device-mobile, .is-device-tablet {


} //- .is-device-mobile, .is-device-tablet


$feature: max-width;
$mediaWidth1: 1365px;
$mediaWidth2: 1279px;
$mediaWidth3: 1023px;
$mediaWidth4: 959px;
$mediaWidth5: 767px;
$mediaWidth7: 639px;
$mediaWidth6: 413px;

/* START #{$mediaWidth1} */ //-1365px
@media ($feature: $mediaWidth1) {
	footer {
		overflow: hidden;
	}
}/* END #{$mediaWidth1} */	//-1365px


/* START #{$mediaWidth2} */ //-1279px
@media ($feature: $mediaWidth2) {

	//- footer
	.footer-sponsor {
		padding: 40px 40px;
	}
	.footer-sponsor-left, .footer-sponsor-right {
		width: 100%;
	}
	.footer-sponsor-left {
		margin-bottom: 50px;
	}
	.footer-sponsor-right {
		padding-left: 0;
	}
	.footer-sponsor-title {
		font-size: 40px;
		line-height: 40px;
		margin-bottom: 20px;
	}
	.footer-blue-social {
		width: 305px;
	}

	.footer-contacts-list {
		flex-wrap: wrap;
	}
	.f-contact-title {
		br {
			display: none;
		}
	}
	.footer-contacts-list {
		li {
			&:nth-child(1) {
				width: 50%;
			}
			&:nth-child(2),
			&:nth-child(3) {
				width: 25%;
			}

				&:nth-child(4) {
					width: 30%;
					margin-bottom: 0;
				}
				&:nth-child(5) {
					width: 20%;
					margin-bottom: 0;
				}		
			margin-bottom: 40px;
		}
	}
	.footer-contacts-list li a {
		padding: 0;
	}
	.footer-bottom {
		width: 50%;
		display: block;
		margin-top: -46px;
		float: right;
	}
	.copy {
		margin-right: 0;
		margin-bottom: 9px;
	}
	.footer-contacts-container {
		padding: 40px;
		padding-bottom: 70px;
	}
	//- footer

	.footer-blue-container, 
	.footer-contacts-container {
		padding-left: 40px;
		padding-right: 40px;				
	}				
}/* END #{$mediaWidth2} */	//-1279px

/* START #{$mediaWidth3} */ //-1023px
@media ($feature: $mediaWidth3) {
	.footer-blue-container, 
	.footer-contacts-container {
		padding-left: 10px;
		padding-right: 10px;				
	}	

	.f-contact-title, .footer-contacts-list li a {
		font-size: 14px;
	}
	.copy {
		font-size: 14px;
	}
	.footer-bottom-link {
		font-size: 14px;
	}		
}/* END #{$mediaWidth3} */	//-1023px

/* START #{$mediaWidth4} */ //-959px
@media ($feature: $mediaWidth4) {
	.footer-blue-social {
		width: 260px;
	}
	.footer-blue-history {
		width: 200px;
	}
	.icon-logo-footer {
		width: 160px;
		height: 60px;
	}
	.footer-contacts-list li:nth-child(2) {
		width: 50%;
	}
	.footer-contacts-list li:nth-child(3),
	.footer-contacts-list li:nth-child(4),
	.footer-contacts-list li:nth-child(5) {
		width: 33.33333333%;
		margin-bottom: 40px;
	}
	.footer-bottom {
		margin: 0;
		width: 100%;
		display: flex;
	}
	.copy {
		margin-bottom: 0;
		margin-right: 30px;
	}	
}/* END #{$mediaWidth4} */	//-959px

/* START #{$mediaWidth5} */ //-767px
@media ($feature: $mediaWidth5) {
	.footer-sponsor-title {
		font-size: 25px;
		line-height: 25px;
	}
	.footer-sponsor {
		padding: 20px 10px;
	}
	.footer-sponsor-list li img {
		height: 45px;
	}
	.footer-sponsor-list {
		margin: 0 -10px;

		li {
			padding: 0 10px;
			width: 50%;

			a {
				height: 105px;
			}
		}
	}
	.footer-blue-logo {
		width: 120px;
	}
	.icon-logo-footer {
		width: 120px;
		height: 50px;
	}
	.footer-blue-content {
		flex-wrap: wrap;
	}
	.footer-blue-title {
		br {
			display: none;
		}
	}
	.footer-blue-history {
		width: calc(100% - 120px);
		text-align: right;
		flex-direction: column;
		display: flex;
		justify-content: space-between;
		padding-top: 3px;
		padding-bottom: 5px;
	}
	.footer-history-list {
		justify-content: flex-end;
		margin-top: 2px;
	}
	.footer-blue-social {
		width: 100%;
		text-align: right;
		margin-top: 30px;
	}
	.footer-social-list {
		justify-content: flex-end;
	}
	.footer-blue-container {
		padding-top: 20px;
		padding-bottom: 20px;
	}
	.footer-contacts-list {
		flex-direction: column;
		align-items: center;

		li {
			width: 270px !important;
			text-align: center;
			margin-bottom: 25px !important;
		}
	}
	.f-contact-email {
		margin-top: 5px;
	}
	.footer-contacts-container {
		padding-top: 20px;
		padding-bottom: 70px;
	}
	.footer-bottom {
		flex-direction: column;
		align-items: center;
		text-align: center;		
	}
	.copy {
		margin-right: 0;
		margin-bottom: 10px;
	}	
}/* END #{$mediaWidth5} */	//-767px