header {
	@extend .d-block;
	position: relative;
	z-index: 5;
}
.header-panel-top {
	@extend .d-block;
	background: $color-violet;
	padding: 8px 0 7px;

	.container {
		display: flex;
		justify-content: space-between;
	}
}

.header-panel-top-left {
	flex-grow: 1;
	min-width: 1px;
	display: flex;
	align-items: center;
}
.header-panel-top-right {
	flex-grow: 1;
	min-width: 1px;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}
.header-panel-list {
	display: flex;
	align-items: center;

	li {
		font: 14px/14px $font-family-halvar-bold;
		white-space: nowrap;
		color: $color-white;
		margin: 0 20px;
		transition: color 0.3s; 
		display: block;

		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}

		&:hover {
			color: $color-blue;
		}

		a {
			font: 14px/14px $font-family-halvar-bold;
			white-space: nowrap;
			color: $color-white;			
			text-decoration: none;
			display: block;

			&:hover {
				color: $color-blue;
			}			
		}
	}
}
.header-panel-top-lang {
	margin-right: 60px;
	display: flex;
	align-items: center;

	a {
		display: flex;
		align-items: center;
		font: 14px/14px $font-family-halvar-bold;
		white-space: nowrap;
		color: $color-white;			
		text-decoration: none;
		transition: color 0.3s; 

		&:hover {
			color: $color-blue;
		}		
	}

}
.header-top-lang-icon {
	margin-right: 9px;
	height: 16px;
}
.header-panel-top-auth {
	display: block;
	a {
		font: 14px/14px $font-family-halvar-bold;
		white-space: nowrap;
		color: $color-white;			
		text-decoration: none;
		display: block;

		&:hover {
			color: $color-blue;
		}			
	}
}
.header-panel-date {
	background: $color-blue2;
	white-space: nowrap;
	text-transform: uppercase;
	color: $color-white;
	height: 20px;
	display: block;
	padding: 0 10px;
	font: 10px/23px $font-family-halvar-regular;
	margin-left: 10px;
	border-radius: 30px;
	letter-spacing: 1px;
}
.header-container {
	@extend .d-block;
	padding-top: 30px;
}
.logo {
	float: left;
	display: block;
	max-width: 163px;
	min-width: 163px;

	a {
		text-decoration: none !important;
	}

	svg {
		width: 163px;
		height: 84px;
	}
}
.header-content {
	display: flex;
	width: 100%;
}
.nav__container {
	width: 100%;
	display: block;	
	padding-left: 140px;
	padding-top: 10px;
}
.nav__content {
	width: 100%;
	display: block;
}
.main-nav {
	display: flex;
	flex-wrap: wrap;
	float: left;

	> li {
		display: inline-block;
		float: left;
		margin-bottom: 11px;
		margin: 0 60px 10px; 

		> a {
			text-transform: uppercase;
			font: 14px/16px $font-family-halvar-bold;
			letter-spacing: 1px;
			color: $color-white;
			white-space: nowrap;
		} 

		&:first-child,
		&:nth-child(5) {
			margin-left: 0;
		}

		&:nth-child(4),
		&:last-child {
			margin-right: 0;
		}


		&.is-active {
			.nav-dropdown-box {
				.sub-nav {
					visibility: visible;
					opacity: 1;
				}
		
		@media (min-width: 1280px) {
				&:hover {
					.sub-nav {
						visibility: visible;
						opacity: 1;
					}
				}					
		}

			}

			.nav-dropdown-box > a::before {
				border-top: 0;
				border-bottom: 6px solid $color-white;
			}

		}		

	}
}
.nav-dropdown-box {
	display: inline-block;
	position: relative;

		> a {
			text-transform: uppercase;
			font: 14px/16px $font-family-halvar-bold;
			letter-spacing: 1px;
			color: $color-white;
			position: relative;
			white-space: nowrap;

			&::before {
				@extend .before;
				border: 4px solid transparent;
			 border-top: 6px solid $color-white; 
			 top: 50%;
			 right: -15px;
			 margin-top: -3px;
			 transition: border 0.3s;
			}

		} 	
	
	@media (min-width: 1280px) {
		&:hover {
			.sub-nav {
				visibility: visible;
				opacity: 1;
			}
		}
	}

}
.sub-nav {
	display: none;
	background: $color-white;
	text-align: left;
	padding: 23px 20px 22px;

	@media (min-width: 768px) {
		display: block;
		position: absolute;
		box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.20);	
		border-radius: 3px;
		left: 50%;
		top: 32px;
		transform: translateX(-50%);
		transition: opacity 0.3s visibility 0.3s;
		opacity: 0;
		visibility: hidden;
		z-index: 20;
	}

	&::before {
		@extend .before;
		width: 14px;
		height: 14px;
		left: 50%;
		top: 0;
		margin-left: -7px;
		margin-top: -7px;
		background: $color-white;
		transform: rotate(45deg); 
	}

	&::after {
		@extend .before;
		width: 100%;
		height: 17px;
		left: 0;
		top: -17px;
	}	

	li {
		@extend .d-block;;
		margin-bottom: 21px;

		&:last-child {
			margin-bottom: 0;
		}

		a {
			display: block;
			color: $color-blue2;
			font-weight: 300;
			white-space: nowrap;
		 font: 14px/14px $font-family-inter-regular;

			&:hover {
				color: $color-blue;
			}

		}
	}
}

.nav-hide {
	display: none;
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 10;
	cursor: pointer;
	left: 0;
	top: 0;
}
.touch-nav {
	display: none;
	width: 35px;
	height: 35px;
	border-radius: 17.5px;
	background: $color-blue;
	margin-left: 20px;
	padding: 9px 7.5px 0;

	i {
		width: 20px;
		height: 3px;
		margin-bottom: 4px;
		border-radius: 1.5px;
		background: $color-white;
		display: block;
		transition: all .3s;

		&:last-child {
			margin-bottom: 0;
		}
	}
	&.is-active {
		i {
			width: 25px;

			&:first-child {
				transform: rotate(45deg);
				transform-origin: 10% 50%;
			}
			&:nth-child(2) {
				opacity: 0;
			}
			&:last-child {
				transform-origin: 10% 50%;
				transform: rotate(-45deg);
			}
		}
	}
}

.nav-close {
	right: 10px;
	top: 8px;
	position: absolute;
	z-index: 20;
	display: none;
	width: 35px;
	height: 35px;
	border-radius: 17.5px;
	padding: 9px 7.5px 0;

	i {
		width: 25px;
		height: 3px;
		margin-bottom: 4px;
		border-radius: 1.5px;
		background: $color-white;
		display: block;
		transition: all .3s;

		&:first-child {
			transform: rotate(45deg);
			transform-origin: 10% 50%;
		}
		&:nth-child(2) {
			opacity: 0;
		}
		&:last-child {
			transform-origin: 10% 50%;
			transform: rotate(-45deg);
			margin-bottom: 0;
		}		
	}

}

//- .page-child
.page-child {
	.nav-dropdown-box > a, .main-nav > li > a {
		color: $color-black;
	}
	.nav-dropdown-box > a::before {
		border-top-color: $color-black;
	}
	.main-nav > li.is-active .nav-dropdown-box > a::before {
		border-bottom-color: $color-black;
	}
}
//- .page-child


.is-device-mobile, .is-device-tablet {
	.nav-dropdown-box:hover {
		.sub-nav {
			visibility: hidden;
			opacity: 0;
		}
	}

} //- .is-device-mobile, .is-device-tablet


$feature: max-width;
$mediaWidth1: 1365px;
$mediaWidth2: 1279px;
$mediaWidth3: 1023px;
$mediaWidth4: 959px;
$mediaWidth5: 767px;
$mediaWidth7: 639px;
$mediaWidth6: 413px;

/* START #{$mediaWidth1} */ //-1365px
@media ($feature: $mediaWidth1) {
	header {
		overflow: hidden;
	}
}/* END #{$mediaWidth1} */	//-1365px

/* START #{$mediaWidth2} */ //-1279px
@media ($feature: $mediaWidth2) {
	.header-panel-top {
		padding-left: 40px;
		padding-right: 40px;
	}
	.main-nav > li {
		margin: 0;
		margin-bottom: 10px;
		width: 33.33333333%;
	}	
}/* END #{$mediaWidth2} */	//-1279px

/* START #{$mediaWidth3} */ //-1023px
@media ($feature: $mediaWidth3) {
	.header-panel-top {
		padding-right: 10px;
		padding-left: 10px;
	}
	
}/* END #{$mediaWidth3} */	//-1023px

/* START #{$mediaWidth4} */ //-959px
@media ($feature: $mediaWidth4) {
	.nav__container {
		padding-left: 30px;
	}
	.main-nav > li {
		&:nth-child(1),
		&:nth-child(4) {
			width: 35%;
		}				
		&:nth-child(2),
		&:nth-child(5) {
			width: 35%;
		}		
		&:nth-child(3),
		&:nth-child(6) {
			width: 30%;
		}
	}

	.logo svg {
		width: 123px;
		height: 65px;
	}		
}/* END #{$mediaWidth4} */	//-959px

/* START #{$mediaWidth5} */ //-767px
@media ($feature: $mediaWidth5) {
.page-child {
	.nav-dropdown-box > a, .main-nav > li > a {
		color: $color-white;
	}
	.nav-dropdown-box > a::before {
		border-top-color: $color-white;
	}
	.main-nav > li.is-active .nav-dropdown-box > a::before {
		border-bottom-color: $color-white;
	}
}
	
	.touch-nav {
		display: block;
	}
	.nav__container {
		top: 0;
		max-height: 100vh !important;
		min-height: 100vh;
		height: 100vh;
		width: 100%;
		position: fixed;
		background: rgba($color-white, 0.7);
		z-index: 999;
		left: 0;
		bottom: 0;
		display: none;
	}
	.nav__content {
		display: block;
		width: 290px;
		-webkit-transform: translateX(300px);
		-moz-transform: translateX(300px);
		-ms-transform: translateX(300px);
		-o-transform: translateX(300px);
		transform: translateX(300px);
		height: 100vh;
		position: fixed;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 2147483647;
		background: $color-blue2;
		transition: transform .3s;


		&.menuShow {
			-webkit-transform: translateX(0);
			-moz-transform: translateX(0);
			-ms-transform: translateX(0);
			-o-transform: translateX(0);
			transform: translateX(0);			
		}
	}
	.main-nav {
		overflow: auto;
		display: block;
		height: 100%;
		padding: 40px 0 0 0;
	}
	.main-nav > li {
		width: 100% !important;
		margin-bottom: 15px;
	}
	.main-nav > li > a, 
	.nav-dropdown-box > a {
		font-size: 15px;
		padding-left: 20px;
	}
	.sub-nav {
		width: 100%;
		float: left;

		&::before {
			display: none;
		}
	}
	.sub-nav li a {
		white-space: normal;
		line-height: 18px;
	}
	.sub-nav li {
		margin-bottom: 15px;
	}
	.sub-nav {
		padding: 18px 20px 22px;
		margin-top: 16px;
	}	
	.nav-close {
		display: block;
	}
	.logo svg {
		width: 93px;
		height: 48px;
	}
	.header-container {
		padding-top: 10px;
	}

	header {
		height: 50px;
		position: fixed;
		z-index: 100;
		left: 0;
		top: 0;
		box-shadow: 0 1px 4px 0px rgba($color-black,.5);
	}
	.header-panel-top {
		height: 50px;
	}
	.page-content {
		display: block;
		margin-top: 50px;
	}
	.navFix {
		.main-bn-wrap {
			z-index: 100;
		}
	}			
}/* END #{$mediaWidth5} */	//-767px